import React, { useState, useEffect } from 'react';

import Filter from './components/Filter';
import Persons from './components/Persons';
import Add from './components/Add';
import Notification from './components/Notification';
import personService from './services/persons';

const App = () => {
	const [ persons, setPersons ] = useState([]);
	const [ newName, setNewName ] = useState('');
	const [ newNumber, setNewNumber ] = useState('');
	const [ newFilter, setNewFilter ] = useState('');
	const [ notification, setNotification ] = useState({});

	useEffect(() => {
		personService.getAll().then((persons) => {
			setPersons({ persons });
		});
	}, []);

	const addEntry = (e) => {
		e.preventDefault();

		//REVIEW THIS!! https://fullstackopen.com/en/part2/altering_data_in_server
		if (persons.find((person) => person.name === newName)) {
			let entry = persons.find((person) => person.name === newName);
			let confirmUpdate = window.confirm(`Do you want to update the phone number for ${entry.name}?`);
			if (confirmUpdate) {
				const newPerson = { name: newName, number: newNumber };

				personService.update(entry.id, newPerson).then((res) => {
					setPersons(persons.map((person) => (person.id === res.id ? res : person)));
				});

				setNotification({ message: `${newName}'s number has been updated`, type: 'notification' });
				setTimeout(() => {
					setNotification(null);
				}, 5000);
				setNewName('');
				setNewNumber('');
			}
		} else if (!persons.some((person) => person.name.toLowerCase() === newName.toLowerCase())) {
			//Creating this object was helpful for me. Do this again!
			const newPerson = { name: newName, number: newNumber };

			personService.create(newPerson).then((res) => {
				setPersons(persons.concat(res));
			});

			setNotification({ message: `${newName} has been added `, type: 'notification' });
			setTimeout(() => {
				setNotification(null);
			}, 5000);

			setNewName('');
			setNewNumber('');
		} else {
			alert(`${newName} is already added to phonebook`);
		}
	};

	const deleteEntry = (id) => {
		//Creating an entry for the listing helped me work this out again!
		//(Note to self: ee how to do this without doing so)
		const entry = persons.find((person) => person.id === id);
		let deleteAlert = window.confirm(`Do you want to delete the entry about ${entry.name}?`);
		if (deleteAlert) {
			personService
				.remove(id)
				.then(() => {
					setPersons(persons.filter((person) => person.id !== id));
				})
				.catch(() => {
					setNotification({
						message: `This person was already removed`,
						type: `error`
					});
					setTimeout(() => {
						setNotification({});
					}, 5000);
				});
		}
	};
	const handleNameChange = (e) => {
		setNewName(e.target.value);
	};

	const handleNumberChange = (e) => {
		setNewNumber(e.target.value);
	};

	const handleFilterChange = (e) => {
		setNewFilter(e.target.value);
	};

	return (
		<div>
			{notification && <Notification message={notification.message} type={notification.type} />}
			<Filter value={newFilter} onChange={handleFilterChange} />
			<Add
				addEntry={addEntry}
				newName={newName}
				handleNameChange={handleNameChange}
				newNumber={newNumber}
				handleNumberChange={handleNumberChange}
			/>
			<Persons persons={persons} deleteEntry={deleteEntry} newFilter={newFilter} />
		</div>
	);
};

export default App;
